import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-bbcnews',
  templateUrl: './bbcnews.component.html',
  styleUrls: ['./bbcnews.component.css']
})
export class BbcnewsComponent implements OnInit {
  response: any;
  hideme = [];
  constructor(private http: HttpClient) { }

  ngOnInit() {

    this.http.get('https://newsapi.org/v2/top-headlines?sources=bbc-news&apiKey=df408c7f56bc4bfb8c2903fe15add076').subscribe((response) => {
      this.response = response;
        let x = this.response.totalResults;
        let title;
        let description;
        let urlToImage;
        let content;
        let author;
        let publishedAt;
        let url;
        for (let i = 0; i < x; i++) {
  
          title = this.response.articles[i].title;
          description = this.response.articles[i].description;
          urlToImage = this.response.articles[i].urlToImage;
          content = this.response.articles[i].content;
          author = this.response.articles[i].author;
          publishedAt = this.response.articles[i].publishedAt;
          url = this.response.articles[i].url;
  
        }
    
      })
    }

  }


